import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-cake',
  templateUrl: './cake.component.html',
  styleUrls: ['./cake.component.scss']
})
export class CakeComponent implements OnInit {
  @Input()  cake:any
  avg:number
  constructor() { 
  }

  ngOnInit() {
    this.avgRate()
  }
  avgRate(){
    let sum = 0;
    for(let i=0; i < this.cake.comments.length; i++){
      sum += this.cake.comments[i].rating
    }
    this.avg = Math.round(sum/(this.cake.comments.length)*10)/10
  }
}
