import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private _http: HttpClient) { }

  getCakes(){
    return this._http.get('/cakes')
  }

  getCakeId(cake){
    return this._http.get(`/cakes/${cake._id}`)
  }

  addCake(newCake){
    return this._http.post('/new/cakes', newCake)
  }

  addComment(comment){
    return this._http.post(`/add/comment/${comment._id}`, comment)
  }

  deleteCake(cake){
    return this._http.delete(`/remove/${cake._id}`, cake)
  }
}
