import { Component, OnInit } from '@angular/core';
import { HttpService } from './http.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{
  title = 'Rate My Cakes!';
  cakes:any
  cake:any
  comment:any
  newCake:any
  errorName:string
  errorImg:string
  errorCom:string
  errorRat:string
  comp:boolean = false
  rating:any
  constructor(private _httpService: HttpService){
    this.cakes = []
    this.cake = {baker: '', image: '', comment: this.comment}
    this.comment = {rating: 5, comment: ''}
    this.errorName = ''
    this.errorImg = ''
    this.errorCom = ''
    this.errorRat = ''
    this.rating = {}
  }

  ngOnInit(){
    this.newCake = {baker: '', image:''}
    this.getCakesFromService()
  }

  getCakesFromService(){
    this._httpService.getCakes()
    .subscribe(data => {
      //@ts-ignore
      for(let cake of data){
        this.rating[cake._id] = {_id: cake._id,rating: 5, comment: ''}
      }
      console.log('Got our cakes!', data)
      this.cakes = data;
      console.log(this.cakes)
    })
  }


  getIdCake(cake){
    this.cake = cake
    this.comp = true;
    console.log(this.cake)
  }
  
  onNewCake(){
    this._httpService.addCake(this.newCake)
    .subscribe((data:any) =>{
      console.log('Got data from post back', data);
      this.newCake = {baker: '', image: ''}
      if(data.errors && data.errors.baker){
        this.errorName = data.errors.baker.message;
      }
      if(data.errors && data.errors.image){
        this.errorImg = data.errors.image.message;
      }
      console.log(this.newCake)
      this.getCakesFromService()
    })
    console.log(this.newCake)
  }

  onNewComment(id){
    this._httpService.addComment(this.rating[id])
    .subscribe((data:any) => {
      console.log('Got data from post back', data);
      this.rating[id] = {rating: 5, comment: ''}
      if(data.errors && data.errors.comment){
        this.errorCom = data.errors.comment.message;
      }
      if(data.errors && data.errors.rating){
        this.errorRat = data.errors.rating.message;
      }
      console.log(this.comment)
      this.getCakesFromService()
    })
    console.log("Check comment")
  }

  onDeleteClick(cake) {
    this._httpService.deleteCake(cake)
    .subscribe(data => {
      console.log('Delete Data', data);
      this.getCakesFromService()
    })
  }
}
